import React from 'react';
import { connect } from 'react-redux';
import { ingToggleWant, ingToggleNotWant } from '../ings-actions'
import './ing-prefs.scss'


function dispatchToProps(dispatch) {
    return {
        want: ing => dispatch(ingToggleWant(ing)),
        notWant: (event, ing) => {
            dispatch(ingToggleNotWant(ing))
            event.stopPropagation()
        }
    };
}

function IngsPrefs({ ing, want, notWant }) {
    const wantClassName = ing.usable && ing.wanted ? ' ing-prefs-wanted' : ''
    const notWantClassName = ing.usable && ing.notWanted ? ' ing-prefs-wanted-not' : ''
    const inactiveClassName = ing.usable ? '' : ' ing-prefs-inactive'

    const classNames = wantClassName + notWantClassName + inactiveClassName

    const wantedImg = ing.usable && ing.wanted
        ? <img src='ings/ings-prefs-want.svg'
            alt='want'
            className='ing-prefs-want'></img>
        : ''

    const notWantedBtn = ing.usable && !ing.notWanted && !ing.wanted
        ? <button className='ing-prefs ing-prefs-want-not'
            onClick={(event) => ing.usable && notWant(event, ing)}>

            <img src='ings/ings-prefs-want-not.svg'
                alt='want-not'
                className='ing-prefs-want-not'></img>
        </button>
        : ''

    return (<div className={'ing-prefs' + classNames}
        onClick={() => ing.usable && want(ing)}>

        {wantedImg}

        <span className={'ing-prefs' + classNames}>
            {ing.name}
        </span>

        {notWantedBtn}
    </div>)
}

export default connect(null, dispatchToProps)(IngsPrefs);