import React from 'react';
import './drink-ings.scss'

function DrinkIngs_({ drink }) {
    return (<ul className='drink-ings'>
        {(drink.ings || [])
            .map((val, idx, arr) => {

                const sep = idx < arr.length - 1
                    ? <li key={drink.id + '-ings-sep' + idx} className='drink-ings drink-ings-sep'>&nbsp;</li>
                    : ''

                return (<>
                    <li key={drink.id + '-ings' + idx}
                        className='drink-ings'>
                        {/* <span className='drink-ings'> */}
                        {val}
                        {/* </span> */}
                        {/* {sep} */}
                    </li>
                    {sep}
                </>)
            })
        }
    </ul>)
}

export default DrinkIngs_